import React, { useState } from 'react';
import { useReadOTP } from 'react-read-otp';

function App() {
  const [otp, setOTP] = useState('');
  useReadOTP(setOTP, {
    enabled: true,
    onError(err) {
      window.alert(err);
    },
  });
  return (
    <div>
      <h3>OTP Screen</h3>
      <input placeholder="Enter otp" value={otp} autoComplete="one-time-code" onChange={(e) => setOTP(e.target.value)} />
    </div>
  );
}

export default App;
